import styled from 'styled-components';

export const Wrapper = styled.div`
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-row: 1/3;
`;

export const Circle = styled.div`
  width: 4px;
  height: 4px;
  border-radius: 100%;
  background-color: ${({ theme }) => theme.colors.lightGrey};
  position: relative;
  &::after,
  &::before {
    display: block;
    content: '';
    width: 100%;
    height: 100%;
    border-radius: 100%;
    position: absolute;
    background-color: ${({ theme }) => theme.colors.lightGrey};
  }
  &::after {
    top: 10px;
  }
  &::before {
    top: -10px;
  }
`;
